@import 'src/styles.module.scss';

.modalContainer {
  @include flex;
  flex-direction: row;
  flex-grow: 1;
  min-height: 10rem;
  position: relative;
}

.modalRightPanel {
  right: 0;
  top: 2rem;
  bottom: 0;
  @include flex;
  flex-direction: column;
  position: absolute;
}

.modalLeftPanel {
  left: 0;
  top: 2rem;
  bottom: 0;
  position: absolute;
}

.modalLeftButton {
  border: solid black 1px !important;
  top: 0;
  left: 0;
  &:global(.ant-btn) {
    box-shadow: none;
    background: none;
  }
  &.disabled {
    border-color: grey !important;
  }
}

.modalRightButton {
  border: solid black 1px !important;
  top: 0;
  right: 0;
  position: absolute;
  &:global(.ant-btn) {
    box-shadow: none;
    background: none;
  }
  &.disabled {
    border-color: #ffffff !important;
  }
}

.githubButton {
  margin-top: auto;
}

:global(.ant-modal-title) {
  height: 2rem;
}

.modalContent {
  margin-top: 1rem;
  margin-left: 2.5rem;
  margin-right: 2.5rem;
}

.extraProjectDetails {
  text-align: center;
  color: $honolulu_blue;
  font-weight: 500;
}

.descriptionContainer {
  position: relative;
}

.description {
  max-height: 40vh;
  overflow-y: auto;
  &:before,
  &:after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 3rem;
  }

  &.downScrollable {
    &:after {
      bottom: 0;
      background: linear-gradient(to bottom, transparent 0%, white 100%);
    }
  }

  &.upScrollable {
    &:before {
      top: 0;
      background: linear-gradient(to top, transparent 0%, white 100%);
    }
  }
}

.techStackSection {
  margin-top: 1rem;
}

.projectDetails {
  padding: 0 .5rem 0 1.5rem;
}


.techStack {
  margin-top: .4rem;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
}

@media screen and (max-width: $mobile_screen_width) {
  .description {
    max-height: unset;
  }

  .modalContent {
    max-height: 55vh;
    overflow-y: auto;
  }
}